.hero-banner{
    width: 100%;
    background-image: url('../../images/backgrounds/hero-banner-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 205px 0;
}

.banner-title h4{
    font-size: 89px;
    font-weight: 700;
    color: #000;
    text-align: center;
    padding-bottom: 10px;
}

.banner-title h4 span{
    color: #2F80ED;
}

.banner-title p{
    font-size: 20px;
    line-height: 30px;
    color: #B0B0B0;
    max-width: 650px;
    margin: 0 auto;
    text-align: center;
}

.jobs-profile-suggestions{
    padding: 80px 0;
}

.jobs-profile-suggestions ul{
    display: flex;
    justify-content: center;
    max-width: 100%;
    flex-wrap: wrap;
    margin: -5px;
}

.jobs-profile-suggestions ul li{
    padding: 5px;
}

.jobs-profile-suggestions ul li a{
    display: block;
    font-size: 16px;
    color: #000;
    background-color: #fff;
    border: 1px solid #E8F2FF;
    padding: 15px 42px;
}


@media (hover:hover){
    .jobs-profile-suggestions ul li a:hover{
        background-color: #2F80ED;
        color: #fff;
    }
}



.job-input{
    flex: 1;
}

.search-jobs-form{
    display: flex;
    max-width: 760px;
    flex-wrap: wrap;
    background-color: #fff;
    border: 1px solid #DFE0DF;
    padding: 6px;
    margin: 0 auto;
}

.search-jobs-form .button-blue{
    width: 160px;
    margin-left: auto;
}
.search-jobs-form .button-blue img{
    vertical-align: sub;
}
.search-jobs-form .job-input{
    position: relative;
    padding-bottom: 0;
}
.search-jobs-form .job-input .form-control{
    border: 0;
    padding-left: 40px;
}

.search-jobs-form .job-input::before{
    position: absolute;
    left: 15px;
    top: 17px;
}

.search-jobs-form .job-title::before{
    content: url('../../images/icons/ic-suitcase.svg');
}

.search-jobs-form .job-location::before{
    content: url('../../images/icons/ic-location.svg');
}

.search-jobs-form .job-title::after{
    content:"";
    width: 1px;
    height: 18px;
    display: inline-block;
    background-color: #DFE0DF;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
}

/*---------------------------------------------
# Job Offers
---------------------------------------------*/


.job-offers{
    background-image: url('../../images/backgrounds/job-offer-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 50px 0;
    overflow: hidden;
}

.job-offers .section-title{
    padding-bottom: 25px;
}

.job-box{
    background-color: #fff;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.13);
    padding: 15px 15px;
    position: relative;
}

.job-types{
    position: absolute;
    top: 15px;
    right:15px;
}

.job-box .employer-logo{
    display: inline-block;
    width: 36px;
    min-width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 1px solid #ccc;
    margin-bottom: 10px;
}

.job-box .button-blue{
    position: relative;
    z-index: 5;
}
.job-title{
    padding-bottom: 15px;
}
.job-title h4{
    font-size: 20px;
    font-weight: 600;
    color: #080036;
}
.job-title p{
    font-size: 14px;
    color: #080036;
}

/* .job-location{
    display: block;
    font-size: 16px;
    color: #545454;
    padding-bottom: 15px;
    padding-left: 20px;
} */


.job-box .job-salary{
    display: block;
    font-size: 16px;
    color: #000000;
}


.jobs-offer-slider .slick-slide{
    padding-right: 20px;
}

.jobs-offer-slider .slick-slider .slick-list{
    padding-top: 10px !important;
    padding-bottom: 10px !important;
}
.jobs-offer-slider .slick-dots > li{
    display: inline-block;
}
.jobs-offer-slider .slick-dots > li > button{
    width: 28px;
    height: 5px;
    background-color: #DFE0DF;
    border-radius: 10px;
    border:0;
    font-size: 0;
}

.jobs-offer-slider .slick-dots > li.slick-active > button{
    background-color: #2F80ED;
    width: 48px;
}


/*---------------------------------------------
# Top Categories
---------------------------------------------*/
.job-category{
    text-align: center;
    padding: 50px 15px;
}
.job-category .category-icon{
    padding-bottom: 15px;
}

.job-category .category-title{
    display: block;
    font-size: 22px;
    line-height: 32px;
    font-weight: 600;
    color: #000000;
    padding-bottom: 10px;
}

.job-category .position-number{
    display: inline-block;
    padding: 5px 10px;
    font-size: 13px;
    color: #545454;
    background-color: #FDFDFD;
    border: 1px solid #EBEBEB;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.08);
}

@media(hover:hover){
    .job-category .position-number:hover{
        background-color: #EAF2FD;
        border-color: #EAF2FD;
    }
}

.categories-list{
    position: relative;
}
.categories-list::after{
    content: '';
    width: 100%;
    height: 1px;
    background-color: #ECECEC;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.categories-list > [class*="col-"]{
    border-right: 1px solid #ECECEC;
}
.categories-list > [class*="col-"]:nth-child(4),
.categories-list > [class*="col-"]:nth-child(8)
{
    border-color: transparent;
}


/*---------------------------------
#   HighPriority Jobs CSS
----------------------------------*/

.high-priority-jobs{
    position: relative;
    width: 100%;
    background-image: url('../../images/backgrounds/priority-jobs-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 230px;
    padding-top: 120px;
    z-index: 1;
    overflow: hidden;
}

.high-priority-jobs .section-title{
    padding-bottom: 160px;
}

.high-priority-jobs::before{
    content: '';
    width: 100%;
    height: 308px;
    background-image: url('../../images/backgrounds/wave-bg-2.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.high-priority-jobs::after{
    content: '';
    width: 100%;
    height: 171px;
    background-image: url('../../images/backgrounds/wave-bg-1.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    bottom: -4px;
    left: 0;
}
.high-priority-jobs-list ul{
    margin-bottom: 50px;
}
.high-priority-jobs-list ul li {
    padding-bottom: 30px;
}
.high-priority-jobs-list ul li:last-child{
    padding-bottom: 0;
}

.high-priority-job-wrapper{
    border: 1px solid #ECECEC;
    padding: 10px;
    display: flex;
    align-items: center;
    position: relative;
}
.high-priority-job-wrapper::before{
    content: url('../../images/icons/ic-star-yellow.svg');
    position: absolute;
    left: 0;
    top: 0;
}
.high-priority-job-wrapper .jobs-details{
    flex: 1;
    padding-right: 170px;
}

.high-priority-job-wrapper .employer-logo{
    width: 100px;
    height: 100px;
    border: 1px solid #ECECEC;
    padding: 12px;
    margin-right: 12px;
}

.job-position{
    font-size: 24px;
    font-weight: 600;
    color: #080036;
}

.job-deadline{
    font-size: 16px;
    color: #B0B0B0;
}

.high-priority-job-wrapper .job-metas{
    padding-top: 6px;
    padding-bottom: 10px;
}

.high-priority-job-wrapper .bookmark-btn{
    position: absolute;
    right: 145px;
    top: 50%;
    transform: translateY(-50%);
}

.high-priority-job-wrapper .button-light-blue{
    font-weight: 400;
    background-color: transparent;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.helped-companies{
    margin-top: -50px;
    position: relative;
    z-index: 2;
    padding-bottom: 120px;
}


.app-download-links > a{
    display: inline-block;
}
.app-download-links > a:first-child{
    margin-right: 20px;
}


.get-app{
    width: 100%;
    background-image: url('../../images/backgrounds/get-app-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    padding-top: 50px;
    padding-bottom: 30px;
}


.job-offers .slick-prev{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -50px;
}

.job-offers .slick-next{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -50px;
}

.job-offers:hover .slick-prev{
    left: 20px;
}

.job-offers:hover .slick-next{
    right: 20px;
}

@media screen and (max-width: 1599px){
    .hero-banner{
        padding: 160px 0;
    }
    .high-priority-jobs::before{
        height: 240px;
    }
    .high-priority-jobs::after{
        height: 130px;
    }
}

@media screen and (max-width: 1400px){
    .banner-title h4{
        font-size: 72px;
    }
    .hero-banner{
        padding: 135px 0;
    }
    .high-priority-jobs{
        padding-top: 70px;
        padding-bottom: 165px;
    }
    .high-priority-jobs::before{
        height: 220px;
    }
    .high-priority-jobs::after{
        height: 115px;
    }
    .helped-companies{
        margin-top: 0;
    }
}


@media screen and (max-width: 1250px){
    .banner-title h4{
        font-size: 60px;
    }

    .high-priority-jobs::before{
        height: 190px;
    }
    .high-priority-jobs::after{
        height: 100px;
    }
}

@media screen and (max-width: 1199px){
    .high-priority-jobs{
        padding-top: 70px;
        padding-bottom: 130px;
    }
    .high-priority-jobs::before{
        height: 170px;
    }
    .high-priority-jobs::after{
        height: 80px;
    }
    .helped-companies{
        padding-bottom: 80px;
    }

    .app-download-links > a{
        max-width: 200px;
    }
}

@media screen and (max-width: 991px){
    .banner-title h4{
        font-size: 52px;
    }
    .high-priority-jobs{
        padding: 50px 0;   
    }
    .high-priority-jobs .section-title{
        padding-bottom: 50px;
    }
    .high-priority-jobs::before,
    .high-priority-jobs::after{
        display: none;
    }
    .helped-companies{
        margin-top: 0;
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .categories-list > [class*="col-"]:nth-child(even){
        border-right-color: transparent;
    }
    .categories-list::after{
        display: none;
    }

    .get-app{
        padding: 50px 0;
    }
    .get-app .section-title{
        padding: 30px 0;
    }

}


@media screen and (max-width: 575px){
    .hero-banner{
        padding-bottom: 70px;
    }
    
    .banner-title h4{
        font-size: 36px;
    }

    .banner-title p{
        font-size: 16px;
        line-height: 24px;
    }
    .jobs-profile-suggestions ul li a{
        padding: 12px 20px;
    }
    .jobs-profile-suggestions{
        padding: 50px 0;
    }

    .search-jobs-form .button-blue{
        width: 120px;
    }

}


@media screen and (max-width: 479px){
    .search-jobs-form .job-input{
        flex: 0 0 100%;
        max-width: 100%;
    }
    .search-jobs-form .job-title{
        border-bottom: 1px solid #ececec;
    }
    .search-jobs-form .job-title::after{
        display: none;
    }
    .search-jobs-form .button-blue{
        width: 100%;
    }
    .job-box .job-salary{
        margin-top: 10px;
        margin-bottom: 15px;
    }
    .job-category{
        padding: 30px 10px;
    }
    .job-category .category-title{
        font-size: 17px;
        line-height: 28px;
    }

    .high-priority-job-wrapper .employer-logo{
        width: 64px;
        height: 64px;
    }
    .high-priority-job-wrapper .jobs-details{
        padding-right: 0;
    }

    .app-download-links > a{
        max-width: 140px;
    }

    .job-position{
        font-size: 18px;
    }
    .high-priority-job-wrapper{
        align-items: flex-start;
    }
    .high-priority-job-wrapper .button-light-blue{
        position: static;
        transform: translateY(0);
        margin-top: 15px;
    }
    .high-priority-job-wrapper .bookmark-btn{
        transform: translateY(0);
        right: 7px;
        top: 5px;
    }
}